.form {
  padding: 0 50px;
  position: relative;
}

.form__heading {
  font-size: 25px;
  text-align: center;
  color: rgb(82 115 230);
  margin-top: 130px;
}
.wrapper {
  max-width: 800px;
  width: 1000px;
  height: 250px;
  margin: auto;
  position: relative;
}
.form__input {
  width: 100%;
  height: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  font-size: 16px;
  resize: none;
}

.form__button {
  display: flex;
  width: 200px;
  border-radius: 10px;
  padding: 10px;
  margin: auto;
  justify-content: center;
  align-items: center;
  background-color: rgb(29, 155, 127);
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  margin-top: 30px;
  outline: none;
  border: 2px solid #e4e0db;
}
.button {
  position: absolute;
  right: 0;
  bottom: -30px;
  padding: 5px;
  border-radius: 5px;
  outline: none;
  width: 80px;
  cursor: pointer;
  background-color: rgb(155, 54, 36);
  color: #fff;
  display: flex;
  margin: auto;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease-in-out;
}
.disable {
  opacity: 0.6;
  cursor: no-drop;
  background-color: gray;
  transition: all 0.4s ease-in-out;
}

.language {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: rgb(82 115 230);
  
}
.languages{
  display: flex;
  gap: 10px;
  max-width: 400px;
  margin: auto;
}
.languages_item {
  display: flex;
  flex: 1;
  max-width: 300px;
  max-height: 200px;
  border-radius: 4px;
  border: 4px solid gray;
  padding: 10px;
  position: relative;

}
.languages_item__selected {

  border-color:  rgb(82, 115, 230);
}
.circle{
  display: flex;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  border: 1px solid;
  border-color:  rgb(82, 115, 230);
}
.circle__selected{
  background-color:  #4BB543;
}
.languages_item_text{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
  color: #fff;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 10px;
}
.languages_item_img{
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.language__item__overlay{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  top: 0;
  left: 0;
  opacity: 0.4;
  cursor: pointer;
}