.stages {
  display: flex;
}

.stages_icon {
}

.stages__modal {
  overflow: hidden;
  display: flex;
  left: -3px;
  width: 100%;
  background-color: #555d5d;
  position: absolute;
  border-radius: 0 10px 10px 0;
  flex-direction: column;
  z-index: 20;
  cursor: default;
  transition: all 0.5s ease-in-out;
  max-width: 2px;
  max-height: 2px;
  opacity: 0;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgb(19, 17, 17);
  opacity: 0.8;
  z-index: 20;
  transition: all 0.5s ease-in-out;
}

.overlay__hide {
  opacity: 0;
  transform: translateX(-100%);
}
.stages__modal__show {
  opacity: 1;
  max-height: 1000px;
  max-width: 1000px;
}
.modal__title {
  color: #fff;
  text-align: center;
  margin: 15px auto;
}
.lessons {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}
.button {
  box-shadow: inset 0px 1px 0px 0px #fce2c1;
  background: linear-gradient(to bottom, #ffc477 5%, #fb9e25 100%);
  background-color: #ffc477;
  border-radius: 11px;
  border: 2px solid #eeb44f;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  padding: 8px 18px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #cc9f52;
}

/* CSS */
.button_21 {
  width: 200px;
  margin: 10px auto;
  align-items: center;
  appearance: none;
  background-color: #3eb2fd;
  background-image: linear-gradient(1deg, #4f58fd, #149bf3 99%);
  background-size: calc(100% + 20px) calc(100% + 20px);
  border-radius: 10px;
  border-width: 0;
  box-shadow: none;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  font-family: CircularStd, sans-serif;
  font-size: 1rem;
  height: auto;
  justify-content: center;
  line-height: 1.5;
  padding: 6px 20px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition:
    background-color 0.2s,
    background-position 0.2s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: top;
  white-space: nowrap;
}

.button_21:active,
.button_21:focus {
  outline: none;
}

.button_21:hover {
  background-position: -20px -20px;
}

.button_21:focus:not(:active) {
  box-shadow: rgba(40, 170, 255, 0.25) 0 0 0 0.125em;
}
.book {
  z-index: 200;
  cursor: pointer;
}
