  box-shadow: rgba(44,187,99,.35) 0 -25px 18px -14px inset,rgba(44,187,99,.25) 0 1px 2px,rgba(44,187,99,.25) 0 2px 4px,rgba(44,187,99,.25) 0 4px 8px,rgba(44,187,99,.25) 0 8px 16px,rgba(44,187,99,.25) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
} */

.button__direction:focus:not(:focus-visible):not(.focus-visible) {
  box-shadow: none;
  outline: none;
}

.button__direction:hover {
  background-color: #2c974b;
}

.button__direction:focus {
  box-shadow: rgba(46, 164, 79, 0.4) 0 0 0 3px;
  outline: none;
}

.button__direction:disabled {
  background-color: #94d3a2;
  border-color: rgba(27, 31, 35, 0.1);
  color: rgba(255, 255, 255, 0.8);
  cursor: default;
}

.button__direction:active {
  background-color: #298e46;
  box-shadow: rgba(20, 70, 32, 0.2) 0 1px 0 inset;
}

.button__show {
  background-color: rgb(18, 74, 146);
}

.button__show:hover {
  opacity: 0.9;
  background-color: rgb(21, 64, 119);
}

.button__show:active {
  background-color: rgb(21, 64, 119);
  box-shadow: rgba(20, 70, 32, 0.2) 0 1px 0 inset;
}

.nav__insert {
  appearance: none;
  width: 140px;
  height: 50px;
  background-color: rgb(121, 53, 22);
  margin-top: 10px;
  flex-wrap: wrap;
  outline: none;
  border-color: rgb(117, 89, 89);

  border: 1px solid rgba(27, 31, 35, 0.15);
  border-radius: 6px;
  box-shadow: rgba(27, 31, 35, 0.1) 0 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, system-ui, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  padding: 6px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
}

.disable {
  opacity: 0.4;
  cursor: no-drop;
  background-color: gray;
  transition: all 0.4s ease-in-out;
}
